import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import {Row,Col} from 'react-flexbox-grid/lib'
import axios from 'axios';
import Logout from './logout';

import {useState,useEffect} from 'react'

function Login({children, ...props}) {
    const [userName, setUserName] = useState("");
const [password, setPassword] = useState("");

        // axios.post("http://locaholhost:8080/api/authenticate",this.state.credentails);
    useEffect(() => {
        // axios.get("http://localhost:8080/home",{withCredentials:true}).then(i=>console.log(i));
        console.log(userName,password);
        
    }, []);

    function handleLoginClick(e){
        e.preventDefault();
        const data = {'Authorization':'Basic '+window.btoa(userName+":"+password)};

        axios.get("http://localhost:8080/login/basic "
        ,{headers:data}).then(i=>console.log(i));
    }
    return (
            <div>
            <AppBar position="fixed" >
                <Toolbar style={{ backgroundColor: '#000' }}>
                    <div>
                        Mechanic 
                    </div>
                    <Logout> </Logout>
                </Toolbar>
            </AppBar>
            <Row style={{"margin":"32px 16px"}}>
                <Col xs>
                    <Card style={{"marginTop":"100px"}}>
                        <CardContent>
                            <TextField value={userName} onChange={(e)=> setUserName(e.target.value)}  placeholder="Username" style={{"display":"block","padding":"10px"}}></TextField>
                            <TextField value={password} onChange={(e)=> setPassword(e.target.value)} placeholder="Password" type="password" style={{"display":"block","padding":"10px"}} ></TextField>
                            <Button variant="contained" color="secondary" onClick={(e)=>handleLoginClick(e)} > Login </Button>   
                        </CardContent>
                    </Card>
                </Col>
                <Col xs>
                    <Card style={{"marginTop":"100px"}}>
                        <CardContent>
                        Welcome    
                        </CardContent>    
                    </Card>
                </Col>
            </Row>
            </div>
        )
}
export default Login;